'use client';

import React, { useEffect, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';
// import { DOMAIN_DATA } from '@/lib/domain-data'; // Uncomment if using external file
import Navbar from '@/components/Navbar';
import Footer from '@/components/Footer';
import AuthModal from '@/components/AuthModal';
import { ArrowLeft, Check, LucideIcon, Globe, Server, Database, PenTool, Share2, Search, Layout, Image, Video, Users, Mic, ClipboardList } from 'lucide-react';

// --- DATA DEFINITION START (Ideally move this to lib/domain-data.ts) ---
const DOMAIN_DATA: Record<string, any> = {
  'web-development': {
    title: "Web Development",
    subtitle: "Architecting the Digital Future",
    description: "We don't just write code; we build digital ecosystems. From high-performance progressive web apps to complex backend architectures, the Web Development domain is the engine room of innovation.",
    image: "https://images.unsplash.com/photo-1555066931-4365d14bab8c?q=80&w=2070&auto=format&fit=crop",
    accent: "#3b82f6",
    role: "As a Web Developer, you will work with modern frameworks like Next.js, React, and Node.js. You'll solve complex architectural challenges and deploy scalable applications.",
    stats: [
      { label: "Projects", value: "15+" },
      { label: "Devs", value: "40+" },
      { label: "Stack", value: "MERN" }
    ],
    curriculum: [
      { title: "Frontend Architecture", desc: "Mastering React, Next.js, and State Management.", icon: "Layout" },
      { title: "Backend Systems", desc: "Building robust APIs with Node.js and Python.", icon: "Server" },
      { title: "Database Design", desc: "SQL vs NoSQL, caching strategies, and ORMs.", icon: "Database" }
    ]
  },
  'content-writing': {
    title: "Content Writing",
    subtitle: "The Voice of Innovation",
    description: "Words have the power to move mountains. Our content team crafts compelling narratives that bridge the gap between complex technology and human understanding.",
    image: "https://images.unsplash.com/photo-1519337265831-281ec6cc8514?q=80&w=2070&auto=format&fit=crop",
    accent: "#a855f7",
    role: "As a Content Writer, you will define the voice of EDC. From technical blogs to catchy social media captions, your words will drive engagement.",
    stats: [
      { label: "Articles", value: "50+" },
      { label: "Reach", value: "10k+" },
      { label: "Campaigns", value: "12" }
    ],
    curriculum: [
      { title: "Technical Writing", desc: "Simplifying complex tech concepts for everyone.", icon: "PenTool" },
      { title: "SEO Strategy", desc: "Optimizing content for search engines.", icon: "Search" },
      { title: "Social Media", desc: "Viral marketing and engagement strategies.", icon: "Share2" }
    ]
  },
  'graphic-design': {
    title: "Graphic Design",
    subtitle: "Visualizing the Impossible",
    description: "Where aesthetics meet function. We craft the visual identity of the organization, ensuring that every pixel communicates our core values of excellence and innovation.",
    image: "https://images.unsplash.com/photo-1626785774573-4b7993143a26?q=80&w=2070&auto=format&fit=crop",
    accent: "#ec4899",
    role: "Designers bring ideas to life. You will create posters, UI mockups, and brand assets that define the look and feel of our events.",
    stats: [
      { label: "Assets", value: "500+" },
      { label: "Identity", value: "100%" },
      { label: "Team", value: "25" }
    ],
    curriculum: [
      { title: "Visual Identity", desc: "Color theory, typography, and layout.", icon: "Image" },
      { title: "UI/UX Design", desc: "Creating intuitive user experiences.", icon: "Layout" },
      { title: "Motion Graphics", desc: "Bringing static designs to life.", icon: "Video" }
    ]
  },
  'event-management': {
    title: "Event Management",
    subtitle: "Orchestrating Memories",
    description: "The heartbeat of our community. We plan, execute, and manage large-scale events that bring entrepreneurs, students, and industry leaders together.",
    image: "https://images.unsplash.com/photo-1511578314322-379afb476865?q=80&w=2069&auto=format&fit=crop",
    accent: "#f97316",
    role: "Event managers are the backbone of operations. You will handle logistics, sponsor relations, and on-ground execution.",
    stats: [
      { label: "Events", value: "20+" },
      { label: "Footfall", value: "5k+" },
      { label: "Sponsors", value: "15+" }
    ],
    curriculum: [
      { title: "Logistics", desc: "Venue management and resource allocation.", icon: "ClipboardList" },
      { title: "Public Relations", desc: "Communicating with speakers and guests.", icon: "Mic" },
      { title: "Team Leadership", desc: "Managing volunteers and sub-teams.", icon: "Users" }
    ]
  }
};
// --- DATA DEFINITION END ---

const ICON_MAP: Record<string, any> = {
  Globe, Server, Database, PenTool, Share2, Search, Layout, Image, Video, Users, Mic, ClipboardList
};

export default function DomainPage() {
  const { slug } = useParams();
  const router = useRouter();
  const [data, setData] = useState<any>(null);
  
  // Auth Logic
  const [user, setUser] = useState<any>(null);
  const [isAuthModalOpen, setIsAuthModalOpen] = useState(false);

  useEffect(() => {
    const storedUserStr = localStorage.getItem('edc_user');
    if (storedUserStr) {
      try {
        setUser(JSON.parse(storedUserStr));
      } catch (e) { console.error(e); }
    }
  }, []);

  useEffect(() => {
    if (slug && typeof slug === 'string' && DOMAIN_DATA[slug]) {
        setData(DOMAIN_DATA[slug]);
    }
  }, [slug]);

  const handleLogout = () => {
    setUser(null);
    localStorage.removeItem('edc_user');
  };
  
  const handleLoginSuccess = (u: any) => {
    setUser(u);
    localStorage.setItem('edc_user', JSON.stringify(u));
  };

  if (!data && !slug) return null;
  if (!data) return (
    <div className="min-h-screen bg-black text-white flex flex-col items-center justify-center">
        <h1 className="text-4xl font-display font-bold">Domain Not Found</h1>
        <button onClick={() => router.back()} className="mt-4 text-[#ccff00] hover:underline">Go Back</button>
    </div>
  );

  return (
    <>
        <AuthModal 
            isOpen={isAuthModalOpen} 
            onClose={() => setIsAuthModalOpen(false)}
            onLoginSuccess={handleLoginSuccess}
        />
        
        <Navbar 
            user={user} 
            onOpenAuth={() => setIsAuthModalOpen(true)}
            onLogout={handleLogout}
            onOpenAdmin={() => {}}
        />

        <div className="min-h-screen bg-[#050505] text-white">
            {/* Hero Section */}
            <div className="relative h-[80vh] w-full overflow-hidden">
                {/* Background Image */}
                <div className="absolute inset-0">
                    <img 
                        src={data.image} 
                        alt={data.title} 
                        className="w-full h-full object-cover opacity-40" 
                    />
                    <div className="absolute inset-0 bg-gradient-to-b from-[#050505]/60 via-[#050505]/80 to-[#050505]"></div>
                </div>

                {/* --- FIX START: Dedicated Button Container --- */}
                {/* This container sits on top of the hero but is aligned with the content grid */}
                <div className="absolute top-28 md:top-32 left-0 w-full z-20 px-6 pointer-events-none">
                    <div className="max-w-7xl mx-auto">
                        <button 
                            onClick={() => router.back()}
                            className="pointer-events-auto flex items-center gap-2 text-gray-400 hover:text-white transition-colors uppercase text-xs font-bold tracking-widest"
                        >
                            <ArrowLeft className="w-4 h-4" /> Back to Home
                        </button>
                    </div>
                </div>
                {/* --- FIX END --- */}

                {/* Centered Content */}
                <div className="relative z-10 h-full max-w-7xl mx-auto px-6 flex flex-col justify-center">
                    <h1 className="text-6xl md:text-9xl font-bold font-display tracking-tighter text-transparent bg-clip-text bg-gradient-to-b from-white to-gray-500 mb-6">
                        {data.title}
                    </h1>
                    <p className="text-xl md:text-2xl text-gray-300 max-w-2xl font-light leading-relaxed border-l-4 pl-6" style={{ borderColor: data.accent }}>
                        {data.subtitle}
                    </p>

                    {/* Stats Grid */}
                    <div className="grid grid-cols-3 gap-8 mt-16 max-w-2xl border-t border-white/10 pt-8">
                        {data.stats.map((stat: any, i: number) => (
                            <div key={i}>
                                <div className="text-3xl md:text-4xl font-bold font-display mb-1" style={{ color: data.accent }}>{stat.value}</div>
                                <div className="text-xs text-gray-500 uppercase tracking-widest">{stat.label}</div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* Content Body */}
            <div className="max-w-7xl mx-auto px-6 py-20 grid grid-cols-1 lg:grid-cols-12 gap-16">
                
                {/* Left Column: Description & Role */}
                <div className="lg:col-span-7 space-y-16">
                    <div>
                        <h3 className="text-3xl font-display font-bold mb-6">About the Domain</h3>
                        <p className="text-gray-400 text-lg leading-relaxed">{data.description}</p>
                    </div>

                    <div>
                        <h3 className="text-3xl font-display font-bold mb-6">Your Role</h3>
                        <div className="bg-[#111] p-8 rounded-2xl border border-white/5">
                            <p className="text-gray-300 text-lg leading-relaxed">{data.role}</p>
                        </div>
                    </div>
                </div>

                {/* Right Column: Curriculum */}
                <div className="lg:col-span-5">
                    <div className="sticky top-32">
                        <h3 className="text-3xl font-display font-bold mb-8">What You'll Learn</h3>
                        <div className="space-y-6">
                            {data.curriculum.map((item: any, idx: number) => {
                                const Icon = ICON_MAP[item.icon] || Check;
                                return (
                                    <div key={idx} className="group bg-[#0A0A0A] p-6 rounded-xl border border-white/5 hover:border-white/20 transition-all duration-300 hover:translate-x-2">
                                        <div className="flex items-start gap-4">
                                            <div className="p-3 rounded-lg bg-white/5 text-white group-hover:bg-white group-hover:text-black transition-colors duration-300">
                                                <Icon className="w-5 h-5" />
                                            </div>
                                            <div>
                                                <h4 className="text-lg font-bold font-display mb-2">{item.title}</h4>
                                                <p className="text-sm text-gray-500 leading-relaxed">{item.desc}</p>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                        
                        <div className="mt-12 p-6 bg-gradient-to-br from-[#ccff00]/10 to-transparent rounded-2xl border border-[#ccff00]/20 text-center">
                            <h4 className="text-white font-bold mb-2">Ready to Join?</h4>
                            <p className="text-sm text-gray-400 mb-6">Applications are open for {data.title}.</p>
                            <button 
                                onClick={() => setIsAuthModalOpen(true)}
                                className="w-full py-3 bg-[#ccff00] hover:bg-[#bceb00] text-black font-bold rounded-lg transition-colors"
                            >
                                Apply Now
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <Footer />
        </div>
    </>
  );
}